/**
 * @class Entry
 * @param {String} id - Unique identifier
 * @param {String} title
 * @param {String} description
 * @param {Date} start
 * @param {Date} end
 * @param {String} category
 * @param {Boolean} is_workout
 */

export default class Workout {
    constructor (tpWworkout) {
      this.id = tpWworkout.workoutId
      this.workoutDay=tpWworkout.workoutDay
      this.title=tpWworkout.title
      this.duration=this._format_time(tpWworkout.totalTimePlanned)
      this.description=tpWworkout.description || ""
      this.structure=tpWworkout.structure
      this.tp_body = tpWworkout
      this.is_workout = this.duration == '' ? false : true
    }

    toEntry(){
        // category = default
        // desc from entry
        // title from entry
        // start = new Date(workoutDay)
        // end = new Date(workoutDay)
        // end needs to be updated in place to get different hour
        let desc = this.description
        if (!desc){
            desc = this.title
        }
        let end_date = new Date(this.workoutDay)
        end_date.setHours(2)
        return [
            "default",
            false,
            desc,
            end_date,
            new Date(this.workoutDay),
            this.title,
            this.duration,
            this
        ]
    }

    _format_time(hours){
        if (hours) {
        // Get the whole number part for hours
        let h = Math.floor(hours);
        
        // Get the fractional part and convert to minutes
        let m = Math.floor((hours - h) * 60);
        
        // Get the remaining fractional part and convert to seconds
        let s = Math.floor(((hours - h) * 60 - m) * 60);
        
        // Pad the numbers with leading zeros if necessary
        let hStr = String(h).padStart(2, '0');
        let mStr = String(m).padStart(2, '0');
        let sStr = String(s).padStart(2, '0');
        
        // Combine into HH:mm:ss format
        return `${hStr}:${mStr}:${sStr}`;
        }
        else return ''
    }
}



