// Frontend interactions with our replicated TP API

const HOST = ''


export async function loadWorkouts(start_date = '2025-01-01', end_date = '2025-12-31') {
    
    const requestOptions = {
        method: "GET",
        redirect: "follow"
      };
      
      let res = await fetch(`${HOST}/api/tp/workouts?start_date=${start_date}&end_date=${end_date}`, requestOptions)
      return res.json()
}

export async function getPerformances(workout_id){
    const requestOptions = {
        method: "GET",
        redirect: "follow"
      };
      
      fetch(`${HOST}/api/tp/peakperf?workout=${workout_id}`, requestOptions)
        .then((response) => response.json())
        .then((result) => console.log(result))
        .catch((error) => console.error(error));
}

export async function updateWorkoutDate(workout){      
      fetch(`${HOST}/api/tp/updateWorkout`, {
        headers: {
            "Content-Type": "application/json",
          },
        method: "POST",
        body: JSON.stringify({"workout": workout})
      })
        .then((response) => response.json())
        .then((result) => console.log(result))
        .catch((error) => console.error(error));
}