// Handles resources from TP 
// this will parse the actual structures
// store it in our local storage etc.

import Workout from "../../factory/workouts";
import { loadWorkouts, updateWorkoutDate } from "./api";
import { compareDates } from "../../utilities/dateutils";

export function refreshTrainingPeaks(store, cb){
    console.log('Pulling workouts from storage');
    // Load workouts
    loadWorkouts().then(data => {
        if (data) {
            // Synchronize new workouts to storage
            syncNewWorkoutsToStorage(data, store);
            // Update workouts in storage
            updateWorkoutsInStorage(data, store);
            // Execute the callback function
            if (typeof cb === 'function') {
                cb();
            }
        }
    }).catch(error => {
        console.error('Error loading workouts:', error);
    });
}

export function dailySyncTP(store, cb){
    // Checks workouts for current day, if workout day != to latest refreshed workout 
    // PUT @TP API
    const today = new Date()
    let todaysWorkouts = store.getDayEntriesArray(today)
    todaysWorkouts.forEach(entry => {
        // Check if an actual workout or just random filler
        let workout = entry.workout
        if (workout.is_workout){
            // prev planned date
            let prevPlanned = workout.workoutDay
            if (!compareDates(prevPlanned, today)){
                console.log(`Found modified entry. Updating at TP.`)
                console.log(entry)
                workout.tp_body.workoutDay = today
                updateWorkoutDate(workout.tp_body)
                // Update internal data to prevent re-sync
                store.updateEntry(entry.id, {
                    workout: new Workout(workout.tp_body)
                })
                alert("Success")
            } else {
                alert("Workout is matching at TP.")
            }
        } 
    });
}





function syncNewWorkoutsToStorage(data, store){
    filterStorage(data, store).forEach(el => {
        let workout = new Workout(el)
        store.createEntry(...workout.toEntry())
    });
}

function updateWorkoutsInStorage(data, store){
    let workoutToEntryMap = findWorkoutsInStorage(data, store)
    data.forEach(item => {
        const entry_id = workoutToEntryMap[item.workoutId];
        if (entry_id !== undefined) {
            let workout = new Workout(item)
            // Only update details, but not the date
            store.updateEntry(entry_id, {
                description: workout.description,
                title: workout.title,
                workout: workout,
                duration: workout.duration
            });
        }
    })
}

function findWorkoutsInStorage(data, store){
    // Returns the actual entry elements per workout
    // workoutId: entryId
    // Filter out the data elements for which we have this workoutId 
    let workoutToEntryMap = store.getActiveEntries().reduce((map, item) => {
        map[item.workout.id] = item.id;
        return map;
    }, {});
    return workoutToEntryMap
}

function filterStorage(data, store){
    /**
     * Only store if not existing - i.e. workout id is not part of list
     */
    let allWorkouts = store.getActiveEntries().map(item => item.workout.id)
    let newWorkouts = data.filter(e => !allWorkouts.includes(e.workoutId))
    console.log(`New workouts:`)
    console.log(newWorkouts)
    return newWorkouts
}


/**
 * We now have Entries = Planned in Calendar & Workout
 * We can easily find objects where Entry Date != Workout Date
 * These will then be synced if 1 day prior - thus updating the planned workout date
 */